.nav-logo {
  height: 70px;
}

.navbar ul li {
  font-size: 1.2rem;
}

.section {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 6rem;
}
.hometext {
  max-width: 400px;
  font-size: 2rem;
  text-align: center;
}

.section img {
  height: 200px;
}

.projects {
  margin-top: 6rem;
}
.projects h1 {
  font-weight: bold;
}

.work {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 3rem;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
}

.work img {
  height: 300px;
}

.work .btn-green {
  background: #749b7a;
}

.card {
  transition: ease-in-out;
  transition-duration: 0.5s;
  margin-top: 2rem;
}

.card:hover {
  transition: ease-in-out;
  transform: scale(1.05);
  transition-duration: 0.5s;
}
